















































































































/* eslint-disable */
// reference---https://blog.logrocket.com/how-to-write-a-vue-js-app-completely-in-typescript/
import * as d3 from 'd3'
import * as utils from '@/assets/ts/c6'
import dataService from "@/service/dataService"
import { Component, Vue, Watch, Prop } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
enum StockSituation { high, low }
const stock = namespace('Stock')
const api = "https://www.alphavantage.co/query"
const apikey = "O6K9YK73WIFU2L6B"

interface Meta {
  high: number,
  low: number,
  open: number,
  timestr: string,
  code: string,
  current: number
}
interface Board {
  symbol: string,
  name: string,
  description: string,
  exchange: string,
  currency: string,
  sector: string,
  industry: string,
  address: string,
  employees: string
}
@Component({
  name: 'C6',
  filters: {
    capitalize: function(value: string): string {
      if (!value) return ''
      value = value.toString()
      return value.charAt(0).toUpperCase() + value.slice(1)
    }
  }
})
export default class C6 extends Vue {
  private radio: string = '1'
  private code: string = ''
  private activeSvg: string = '1d'
  private possibilities: string[] = []
  private suggestions: string[] = []
  private svgs: Array < object > = [{
    name: '1 day',
    id: '1d'
  }, {
    name: '5 days',
    id: '5d'
  }, {
    name: '1 month',
    id: '1m'
  }, {
    name: '6 months',
    id: '6m'
  }, {
    name: '1 year',
    id: '1y'
  }]
  private tags: Array < string > = ['open', 'high', 'low']
  @stock.State
  public meta!: Meta
  @stock.State
  public record!: Board
  @stock.State
  public overview:any
  @stock.Action
  public updateStock: (a:any)=>void
  get comparison(): string {
    let delta = this.meta.current - this.meta.open
    let symbol = delta > 0 ? '+' : (delta < 0 ? '-' : ' ')
    let valueText = Math.abs(delta).toFixed(2)
    let arrow = delta > 0 ? '↑' : (delta < 0 ? '↓' : '☁')
    let ratio = Math.abs(delta / this.meta.open) * 100
    let ratioText = ratio.toFixed(2) + '%'
    return `${symbol} ${valueText} (${ratioText})${arrow}`
  }
  get flagColor(): string {
    let green = '#009359',
      red = '#f11b11',
      black = '#000000'
    let delta = this.meta.current - this.meta.open
    return delta > 0 ? green : (delta < 0 ? red : black)
  }
  @Watch('code', {
    immediate: true,
    deep: true
  })
  public codeChanged (newv: string, oldv: string): void {
    if(newv.length < 3) {
      this.possibilities = []
      this.suggestions = []
      return
    }
    this.searchSuggestions(newv)
  }
  public plot(): void {
    let status = this.meta.current - this.meta.open > 0 ? StockSituation.high : StockSituation.low
    this.svgs.forEach((v: object) => {
      utils.init(this.record[v.id], `svg_${v.id}`, status)
    })
    d3.selectAll('.mask').classed('active', true)  // show the overview div & svg
  }
  public searchSuggestions(partial: string): Promise<any> {
    let url = `${api}?function=SYMBOL_SEARCH&keywords=${partial}&apikey=${apikey}`
    return dataService.get(url)
        .then((res: any) => {
          if( 'bestMatches' in res) {
            this.suggestions = res.bestMatches.map((v: any) => `${v['1. symbol']} (${v['2. name']})`)
            this.possibilities = res.bestMatches.map((v: any) => `${v['1. symbol']}`)
          }
          else {
            setTimeout( () => { 
              let url = `${api}?function=SYMBOL_SEARCH&keywords=${partial}&apikey=${apikey}`
              dataService.get(url).then((res: any) => {
                this.suggestions = res.bestMatches.map((v: any) => `${v['1. symbol']} (${v['2. name']})`)
                this.possibilities = res.bestMatches.map((v: any) => `${v['1. symbol']}`)
              })
            }, 60000)
          }
      })
        .catch(err => { console.log(err) })
  }
  public getData(): Promise<any> {
    this.possibilities = []
    this.suggestions = []
    return dataService.hostGet(this.code).then((data)=>{
      this.updateStock(data)
      this.plot()
    }, (rej) => {
      alert('Failed to Fetch Data')
    })
  }
  public async launch(): Promise<any> {
    console.log(this.code, 'press button')
    if (this.code.length < 3) {
      await this.searchSuggestions(this.code)
    }
    if (!this.possibilities.includes(this.code.toUpperCase())) {
      alert('The code you are requesting does not exist :-(.')
      return
    }
    return this.getData()
  }
  mounted() {
    utils.initializeParameter('svg_1d')
  }
}
